import React from "react";
import ReactDOM from "react-dom/client";
import App from "./App";

const root = ReactDOM.createRoot(
  document.getElementById("root") as HTMLElement
);
root.render(
  <React.StrictMode>
    <div id="fullscreen" className="fullscreen">
      <div id="root">
        <App />
      </div>
    </div>
  </React.StrictMode>
);
